/**
 *  Hook
 * @param {Function} apiFunc - 
 * @param {number} [interval=1000] - 
 * @param {number} [maxRetries=0] - 0 
 * @returns {Object} 
 * @returns {Function} returns.startTask - 
 * @returns {Function} returns.stopTask - 
 * @returns {Function} returns.onStart - 
 * @returns {Function} returns.onSuccess - 
 * @returns {Function} returns.onFail - 
 * @returns {Function} returns.onTimeout - 
 * @returns {Function} returns.onComplete - 
 * 
 * @example
 * // 
 * const { startTask, stopTask, onSuccess, onFail } = usePollingTask(
 *   async () => {
 *     const response = await fetch('/api/task-status');
 *     return response.json();
 *   },
 *   2000,  // 2
 *   10     // 10
 * );
 * 
 * // 
 * onSuccess((response, retryCount) => {
 *   console.log(`${retryCount}:`, response);
 *   if (response.status === 'completed') {
 *     stopTask(); // 
 *   }
 * });
 * 
 * // 
 * onFail((error, retryCount) => {
 *   console.error(`${retryCount}:`, error);
 * });
 * 
 * // 
 * startTask();
 * 
 * @example
 * // 
 * const polling = usePollingTask(
 *   async () => {
 *     const response = await fetch('/api/process-status');
 *     return response.json();
 *   },
 *   3000,  // 3
 *   0      // 
 * );
 * 
 * // 
 * polling.onStart((retryCount) => {
 *   console.log(`${retryCount}`);
 * });
 * 
 * polling.onSuccess((response, retryCount) => {
 *   console.log(`${retryCount}:`, response);
 *   if (response.progress === 100) {
 *     polling.stopTask();
 *   }
 * });
 * 
 * polling.onFail((error, retryCount) => {
 *   console.error(`${retryCount}:`, error);
 * });
 * 
 * polling.onTimeout((response, retryCount) => {
 *   console.log(`${retryCount}`);
 * });
 * 
 * polling.onComplete((status, retryCount) => {
 *   console.log(`: ${status}${retryCount}`);
 * });
 * 
 * // 
 * polling.startTask();
 * 
 * // 
 * // useEffect(() => {
 * //   return () => polling.stopTask();
 * // }, []);
 */
export const usePollingTask = (apiFunc, interval = 1000, maxRetries = 0) => {
  let retryCount = 0
  let pollingTimeout = null
  let status = "idle" // idle, polling, stopped, timeout, error
  let isPolling = false

  // 
  let onStartCallback = null
  let onSuccessCallback = null
  let onFailCallback = null
  let onTimeoutCallback = null
  let onCompleteCallback = null

  /**
   * 
   * @param {Function} cb - 
   */
  const onStart = (cb) => {
    if (typeof cb === "function") onStartCallback = cb
  }

  /**
   * 
   * @param {Function} cb - 
   */
  const onSuccess = (cb) => {
    if (typeof cb === "function") onSuccessCallback = cb
  }

  /**
   * 
   * @param {Function} cb - 
   */
  const onFail = (cb) => {
    if (typeof cb === "function") onFailCallback = cb
  }

  /**
   * 
   * @param {Function} cb - 
   */
  const onTimeout = (cb) => {
    if (typeof cb === "function") onTimeoutCallback = cb
  }

  /**
   * 
   * @param {Function} cb - 
   */
  const onComplete = (cb) => {
    if (typeof cb === "function") onCompleteCallback = cb
  }

  /**
   * 
   */
  const stopTask = () => {
    if (pollingTimeout) {
      clearTimeout(pollingTimeout)
      pollingTimeout = null
    }
    isPolling = false
    status = "stopped"
  }

  /**
   * 
   * @returns {Promise<void>}
   */
  const startTask = async () => {
    if (isPolling) return
    isPolling = true
    status = "polling"
    retryCount = 0
    await poll()
  }

  /**
   * 
   * @returns {Promise<void>}
   */
  const poll = async () => {
    if (!isPolling) return
    try {
      status = "polling"
      onStartCallback && onStartCallback(retryCount)
      const response = await apiFunc()
      onSuccessCallback && onSuccessCallback(response, retryCount)
      retryCount++
      if (maxRetries === 0 || retryCount < maxRetries) {
        pollingTimeout = setTimeout(poll, interval)
      } else {
        stopTask()
        status = "timeout"
        onTimeoutCallback && onTimeoutCallback(response, retryCount)
      }
    } catch (error) {
      stopTask()
      status = "error"
      onFailCallback && onFailCallback(error, retryCount)
      onTimeoutCallback && onTimeoutCallback(error, retryCount)
    } finally {
      onCompleteCallback && onCompleteCallback(status, retryCount)
    }
  }

  return {
    startTask,
    stopTask,
    onStart,
    onSuccess,
    onFail,
    onTimeout,
    onComplete,
  }
}
