/**
 * @desc :   %  
 * @desc : flattenToNestedObject
 */
import { useRouteCacheStore } from "@/store/useRouteCacheStore"

/**
 * @file useNavigate.js
 * @description Tab
 */

/**
 * 
 * @param {Object} params - 
 * @returns {string} 
 * @example
 * // 
 * objectToQueryParams({ name: 'John', age: 25 })
 * // : "name=John&age=25"
 *
 * // 
 * objectToQueryParams({ tags: ['js', 'vue'] })
 * // : "tags[]=js&tags[]=vue"
 *
 * // 
 * objectToQueryParams({ user: { name: 'John', age: 25 } })
 * // : "user.name=John&user.age=25"
 */
export const objectToQueryParams = (params) => {
  const encode =
    typeof window !== "undefined"
      ? encodeURIComponent
      : (val) => encodeURIComponent(val)

  const encodeValue = (val) => {
    const encoded = encode(val)
    //  %  %25[2,4](@ref)
    return encoded.replace(/%/g, "%25")
  }

  return Object.keys(params)
    .map((key) => {
      const value = params[key]
      const encodedKey = encode(key)

      if (Array.isArray(value)) {
        return value
          .map((val) => `${encodedKey}[]=${encodeValue(val)}`)
          .join("&")
      } else if (typeof value === "object" && value !== null) {
        return Object.keys(value)
          .map(
            (subKey) =>
              `${encodedKey}.${encode(subKey)}=${encodeValue(value[subKey])}`,
          )
          .join("&")
      } else {
        return `${encodedKey}=${encodeValue(value)}`
      }
    })
    .filter((part) => part.length > 0)
    .join("&")
}

/**
 * 
 * @param {Object} { user.name=John&user.age=25 }   - 
 * @returns {Object} 
 * @example
 * // 
 * flattenToNestedObject("user.name=John&user.age=25")
 * // : { user: { name: 'John', age: 25 } }
 */
export const flattenToNestedObject = (flatObj) => {
  const nestedObj = {}

  for (const [key, value] of Object.entries(flatObj)) {
    //  "goodsDeatilData.attrGroup" -> ["goodsDeatilData", "attrGroup"]
    const keys = key.split(".")
    let current = nestedObj

    // 
    for (let i = 0; i < keys.length - 1; i++) {
      const currentKey = keys[i]
      // 
      if (!current[currentKey]) {
        current[currentKey] = {}
      }
      // 
      current = current[currentKey]
    }

    // 
    const lastKey = keys[keys.length - 1]
    current[lastKey] = decodeURIComponent(value)
  }

  return nestedObj
}

/**
 * 
 * @param {string} path - "/pages/index/index"
 * @param {Object} [params={}] - 
 * @returns {Promise} uni.navigateToPromise
 * @example
 * // 
 * useNavigate('/pages/detail/detail')
 *
 * // 
 * useNavigate('/pages/detail/detail', { id: 123, type: 'product' })
 * // : /pages/detail/detail?id=123&type=product
 */
export const useNavigate = (path, params = {}) => {
  // 
  const queryString = objectToQueryParams(params)

  // URL
  const url = queryString ? `${path}?${queryString}` : path

  return uni.navigateTo({ url })
}

/**
 *  Tab 
 * @param {string} path - Tab pages.json  tabBar 
 * @param {Object} [params] - Tab
 * @returns {Promise} uni.switchTabPromise
 * @example
 * // Tab
 * useSwitchTab('/pages/index/index')
 *
 * // Tab
 * useSwitchTab('/pages/user/user', { refresh: true })
 */
export const useSwitchTab = (path, params) => {
  const { onSaveRouteCache } = useRouteCacheStore()
  onSaveRouteCache({
    key: path,
    data: params,
  })
  return uni.switchTab({
    url: path,
    fail: (error) => {
      console.log(error)
    },
  })
}

/**
 * 
 * @param {string} path - 
 * @param {Object} [params] - 
 * @returns {Promise} uni.redirectToPromise
 * @example
 * // 
 * useRedirectTo('/pages/login/login')
 *
 * // 
 * useRedirectTo('/pages/result/result', { status: 'success' })
 * // : /pages/result/result?status=success
 */
export const useRedirectTo = (path, params) => {
  const queryString = objectToQueryParams(params)
  const url = queryString ? `${path}?${queryString}` : path
  return uni.redirectTo({ url })
}


export const useNavigateBack = () => {
  uni.navigateBack()
}
