import { useUserStore } from "@/store/useUserStore"
import { objectToQueryParams } from "@/hooks/useNavigate"

const showErrorToast = (message = "") => {
  uni.showToast({
    icon: "none",
    title: message,
  })
}

/**
 * httphttpGet, httpPost,  httpPut, httpDelete
 * 
 * jsuni.requesthttphttprequest  apijsrequest
 * PSinterceptorsrequest.js
 * @param options
 */
export const http = (options) =>
  new Promise((resolve, reject) => {
    const userStore = useUserStore()
    uni.request({
      // uniapprequest
      ...options,
      dataType: "json",
      // #ifndef MP-WEIXIN
      responseType: "json",
      // #endif
      // *********************
      success(res) {
        // console.log(res)  //  cookies data header statusCode datacode,msg,data
        const { statusCode, data } = res
        if (statusCode >= 200 && statusCode < 300) {
          resolve(data)
        } else if (statusCode === 401) {
          userStore.clearUserInfo() // 
          uni.navigateTo({ url: "/pages/login/index" }) // 
          reject(res)
          // #ifdef H5
          sessionStorage.removeItem("token")
          // #endif
        } else {
          if (!options.hideErrorToast) {
            showErrorToast(data.msg || "Request error")
          }
          reject(res)
        }
      },
      // ********************
      fail(err) {
        // 
        if (err.errMsg && err.errMsg.includes("timeout")) {
          showErrorToast("Timeout")
        } else {
          showErrorToast("Network Error")
        }
        reject(err)
      },
    })
  })

export const httpGet = (url, data = {}) => http({ url, data, method: "GET" })

export const httpPost = (url, data) => http({ url, data, method: "POST" })

export const httpPut = (url, data) => http({ url, data, method: "PUT" })

export const httpDelete = (url, data = {}) => {
  const queryParams = objectToQueryParams(data)
  const finalUrl = queryParams ? `${url}?${queryParams}` : url
  return http({ url: finalUrl, method: "DELETE" })
}

/**
 *  
 * @param option
 * @returns {Promise<unknown>}
 */
export const request = (option) => {
  let { method, url, data } = option
  if (!url.startsWith("/")) {
    url = "/" + url
  }
  url = url.replace(/\/+/g, "/")

  switch (method.toLowerCase()) {
    case "get":
      return httpGet(url, data)
    case "post":
      return httpPost(url, data)
    case "put":
      return httpPut(url, data)
    case "delete":
      return httpDelete(url, data)
    default:
      throw new Error(`: ${method}`)
  }
}
